<template>
	<view>
		<view class="top">
			<view style="background-color: #7900B2;width: 100%;height: 128px;">
				<view style="width: 100%;" :style="'height:' + system_top + 'px;'"></view>
				<view class="row" style="width: 100%;height: 40px;align-items: center;">
					<image @click="doBack()" src="../../../static/back-white.png" mode="aspectFit"
						style="width: 32px;height: 32px;margin-left: 12px;"></image>
					<view style="margin-left: 5px;flex: 1;" class="column">
						<view style="color: #FFF;font-size: 16px;font-weight: bold;height: 20px;line-height: 20px;">
							{{i18n.meishi.waisong}}
						</view>
						<view style="color: rgba(244, 245, 246, 0.6);font-size: 12px;height: 20px;line-height: 20px;">
							89 Sunkhumvit 19 Alley
						</view>

					</view>
					<image src="../../../static/shoucang.png" mode="" style="width: 32px;height: 32px;"
						:style="'margin-right:' + weixinMenuWidth + 'px;'" @click="doCollection()"></image>
				</view>
			</view>
			<view class="row topSearchBtn" @click="doSearch()">
				<image src="../../../static/search.png" mode="aspectFit"
					style="margin-left: 10px;width: 20px;height: 20px;"></image>
				<view style="margin-left: 10px;font-size: 14px;color: #6F6F6F;">
					{{i18n.index.search}}
				</view>
			</view>
		</view>
		<!-- <image class="bigImage" src="" mode=""></image> -->
		<view class="bigImage">
			<swiper class="index-banner swiper" circular="false" :indicator-dots="indicatorDots" :autoplay="autoplay"
				:interval="interval" :duration="duration">
				<swiper-item class="index-banner-img" @click="bannerTo(item)" v-for="(item, index) in bannerList"
					:key="item.id">
					<image class="index-banner-img" :src="item.image" mode="aspectFill"></image>
				</swiper-item>
			</swiper>
		</view>
		<view class="smallImg_back row" v-if="recommend.length > 0">
			<image class="smallImage" :src="recommend[0].thumb" mode="aspectFill"></image>
			<image v-if="recommend.length > 1" class="smallImage" style="margin-left: 10px;" :src="recommend[1].thumb"
				mode="aspectFill"></image>
		</view>

		<scroll-view scroll-x="true"
			style="width: 100%;height: 248px;white-space: nowrap;background-color: #FFF;margin-top: 20px;" class="row">
			<view v-for="(item,index) in itemArray" :key="index" class="row"
				style="width: 100%;height: 100%;display: inline-block;">
				<view class="row" style="flex-wrap: wrap;width: 100%;height: 100%;">
					<view v-for="(obj,i) in item" class="column array3Item" @click="doMeishiClass(obj)">
						<image :src="obj.thumb" mode="aspectFit" style="width: 56px;height: 56px;">
						</image>
						<view
							style="font-size: 14px;font-weight: bold;color: #383838;margin-top: 6px;text-align: center;">
							{{obj.use_name}}
						</view>
					</view>
				</view>

			</view>
		</scroll-view>
		<view style="width: 60%;height: 60px;align-items: center;line-height: 60px;padding-left: 15px;">
			{{i18n.meishi.fujin}}
		</view>
		<storeList :listArray="nearArray" :height="scrollHeight" typeString="9999" @gotoCell="gotoCell"></storeList>
	</view>
</template>

<script>
	import storeList from '../../../components/storeList/index.vue'
	export default {
		components: {
			storeList
		},
		data() {
			return {
				returnIndex: 1,
				system_top: 44,
				weixinMenuWidth: 12,
				itemArray: [],
				scrollHeight: 0,
				bannerList: [],
				recommend: [],
				nearArray: []
			}
		},
		computed: {
			i18n() {
				return this.$t('index')
			},
		},
		onReady() {
			const res = uni.getSystemInfoSync();

			// #ifdef MP-WEIXIN
			let weinfo = uni.getMenuButtonBoundingClientRect();
			this.weixinMenuWidth = weinfo.width + 12
			// #endif
			if (parseInt(res.safeArea.top) > this.system_top) {
				this.system_top = parseInt(res.safeArea.top)
			}
			this.scrollHeight = res.windowHeight - 60 - this.system_top
		},
		onLoad(option) {
			this.returnIndex = option.rdex
			this.requestData()
		},
		methods: {
			requestData() {
				var that = this
				that.http.sendRequest('FineFood.Home', {}).then(function(res) {
					if (res.code == 0) {
						that.bannerList = res.info[0].banner
						that.recommend = res.info[0].recommend
						that.nearArray = res.info[0].distance_store
						const a = res.info[0].store_type
						var page;
						if (a.length % 8 == 0) {
							page = a.length / 8
						} else {
							page = parseInt(a.length / 8) + 1
						}
						for (let i = 0; i < page; i++) {
							var aa = [];
							for (let j = 0; j < 8; j++) {
								if ((j + i * 8) < a.length) {
									aa.push(a[j + i * 8])
								}
							}
							that.itemArray.push(aa)
						}
						console.log(that.itemArray)
					}
				})


				// if(that.array3.length % 8 == 0)
				// that.array3Page
			},
			doBack() {
				uni.navigateBack({
					delta: this.returnIndex
				})
			},
			gotoCell(item) {
				uni.navigateTo({
					url: '../store/store'
				})
			},
			bannerTo(item) {
				if(item.url.length > 6){
					uni.setStorage({
						key: 'weburl',
						data: item.url,
						success() {
							uni.navigateTo({
								url: '../webView/webView',
							});
						}
					})
				}
			},
			doSearch() {
				uni.navigateTo({
					url: '../search/index'
				})
			},
			doMeishiClass(item) {
				uni.navigateTo({
					url: '../class/class?id=' + item.id + '&name=' + item.use_name
				})
			},
			doCollection() {
				uni.navigateTo({
					url: '../../../package-mine/pages/xihuan/xihuan'
				})
			},
		}
	}
</script>

<style>
	page {
		width: 100%;
		height: 100%;
		background-color: #f5f6f7;
	}

	.top {
		width: 100%;
		height: 160px;
		position: relative;

	}

	.topSearchBtn {
		position: absolute;
		left: 15px;
		right: 15px;
		bottom: 15px;
		height: 40px;
		border-radius: 8px;
		background-color: #FFF;
		/* box-shadow: rgba(50,50,59,0.1) 10px 10px; */
		-moz-box-shadow: 0 0 5px 5px rgb(0, 0, 0, 0.04);
		-webkit-box-shadow: 0 0 5px 5px rgb(0, 0, 0, 0.04);
		box-shadow: 0 0 5px 5px rgb(0, 0, 0, 0.04);
		align-items: center;
	}

	.bigImage {
		margin-left: 15px;
		width: calc(100% - 30px);
		border-radius: 8px;
		height: 130px;
		background-color: #7900B2;
		transform: translateY(0);
		overflow: hidden;
	}

	.index-banner-img {
		width: 100%;
		height: 100%;
		border-radius: 8px;

	}

	.index-banner image {
		width: 100%;
		height: 100%;
		border-radius: 8px;
	}

	.smallImg_back {
		margin-top: 10px;
		margin-left: 15px;
		width: calc(100% - 30px);
		height: 80px;
	}

	.smallImage {
		width: calc(50% - 5px);
		border-radius: 8px;
		height: 80px;
		background-color: #7900B2;
	}

	.array3Item {
		margin-top: 10px;
		width: calc(25% - 8.1px);
		margin-right: 8px;
		height: 100px;
		border-radius: 8px;
		justify-content: center;
		align-items: center;
		margin-bottom: 10px;
		background-color: #F5F6F7;
		border-radius: 8px;
	}
</style>
